.cover {
  -o-object-fit: cover;
     object-fit: cover;
}

.work-card {
  overflow: hidden;
  position:relative;
}

.work-card img {
  width: 100%;
  height: 100%;
}

.work-card h4 {
  font-family: 'SF Display';
  font-size: 84px;
  margin: 0 0 0 0;
  color: white;
  line-height: 100px;
}

.work-card .card-info {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  position: absolute;
  bottom: 120px;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
  -webkit-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  -o-transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
  transition: all 300ms cubic-bezier(0.645, 0.045, 0.355, 1);
}

.card-info.light h4 {
  color: black;
}

.card-info.light p {
  color: black;
}

.card-info.light p.link-label {
  color: #E61E4D;
}

.work-card:hover .card-info {
  -webkit-transform: translateY(-10px);
      -ms-transform: translateY(-10px);
          transform: translateY(-10px);
}

.first-row .work-card .card-info {
  top: 280px;
}

.second-row .card-info {
  margin-left: 880px;
  bottom: 280px;
}

.work-card p {
  font-family: 'SF Text';
  font-size: 24px;
  line-height: 40px;
  color: white;
  margin-top: 20px;
  margin-bottom: 40px;
}

.work-card p.time-label {
  color: rgba(255,255,255,0.5);
  margin-top: 0px;
}

.card-info.light p.time-label {
  color: #9f9f9f;
}

.first-row .work-card p {
  margin-right: 700px;
}

.second-row .work-card p {
  margin-right: 540px;
}

.work-card p.link-label {
  font-family: 'SF Light';
  font-size: 24px;
  line-height: 40px;
  color: #FFF500;
}

.work-tag {
  display: inline-block;
  border-radius: 90px;
  border-color: white;
  border-width: 1px;
  border-style: solid;
  margin-right: 20px;
}

.work-tag.design {
  border-color: #FFF500;
  background-color: rgba(255, 245, 0, 0.25);
}

.work-tag.dev {
  border-color: #FF00C7;
  background-color: rgba(255, 0, 199, 0.25);
}

.work-tag p {
  margin: 0 16px 0 16px;
}



